import React from 'react'
import { Mutation } from "react-apollo";
import {gql} from 'apollo-boost'
import FavoriteIcon from '@material-ui/icons/Favorite';

import { makeStyles } from '@material-ui/core/styles';
import clsx from 'clsx';
import IconButton from '@material-ui/core/IconButton';
import {Context} from '../usercontext'
const useStyles = makeStyles(theme => ({
   
     expand: {
       transform: 'rotate(0deg)',
       marginLeft: 'auto',
       transition: theme.transitions.create('transform', {
         duration: theme.transitions.duration.shortest,
       }),
     },
     expandOpen: {
       transform: 'rotate(180deg)',
     },
   
   }));


export default function Createlike({ expanded, setExpanded, likes,tweetid, setLoading, CREATE_LIKE_MUTATION,refetchQuery, username, page}) {
  console.log(page)
  const cuser=React.useContext(Context)
  const classes = useStyles();
   let handleLikeCreate = (createLike) => {

     setExpanded('likes')
     if (cuser)
     createLike()
   }
    return (
       
        <Mutation mutation={CREATE_LIKE_MUTATION}
        variables={{tweetid:tweetid}}
        onCompleted={data => {
        
        }}
        refetchQueries={() =>{
          if (username)
          return [{ query: refetchQuery, variables:{username:username, page:page} }]
          else
          return [{ query: refetchQuery, variables:{page:page}}]
        }} 
        
    >
    
            {(createLike, {loading, error})=> {
            
              if (loading) setLoading(true)
              else {
                setLoading(false)
              }
                if (error) console.log(error)
                return (
                    <span>
                    {likes.length}
                  <IconButton 
                    className={clsx(classes.expand, {
                      [classes.expandOpen]: expanded=="likes",
                    })}
                    onClick={()=>handleLikeCreate(createLike)}
                    aria-expanded={expanded}
                    aria-label="Show more"
                  style={{paddingLeft:"5px"}} aria-label="Share">
                   <FavoriteIcon />
                  </IconButton>
                  </span>
                )
            }
        }

        </Mutation>
    )
}

