 function CustomizedMenus(props) {
 

 

  
  return (
   
        <Query query={GET_SEARCH_USER}
        variables={{searchtext:props.searchText}}
        >
    {({ loading, error, data }) => {
       
      if (loading) return <Loading />
      if (error) return `Error! ${error.message}`;

      return (
        <div style={ data.searchuser.length > 10 ? { 
          border: '1px solid #d3d4d5',    maxHeight: "300px",
          overflow: "auto"} : {border: '1px solid #d3d4d5'}}>
   
      
      <List 
     
      style={{    background: "#fff", zIndex: "999"}}>
        {data.searchuser.length == 0 ? (
            <ListItem>
                     
              <ListItemText  secondary={"No Users Found"} />
            </ListItem>
        ) : (
          data.searchuser.map(item => {
            return (
           <>
              <ListItem onClick={()=>{
                props.goToProfile(item.id)
              }}>
              
              <ListItemAvatar>
            <Avatar
              
              src={`${STATIC_URL}${item.profileSet[0].profilePic}`}
            />
          </ListItemAvatar>
                <ListItemText  secondary={item.username} />
              
              </ListItem>
                <Divider variant="inset" component="li" />
                </>
            
            )
          })
        )}
     
      
       
      </List>
      </div>
         );
        }}
    
      </Query>
  
  );
}



const GET_SEARCH_USER = gql`
query($searchtext:String!){
  searchuser(searchtext:$searchtext) {
    username
    id
    profileSet {
      profilePic
    }
    
  }
}
`

