import React, { useState } from "react";
import { Mutation } from "react-apollo";
import { gql } from "apollo-boost";
import withStyles from "@material-ui/core/styles/withStyles";
import Typography from "@material-ui/core/Typography";
import Avatar from "@material-ui/core/Avatar";
import FormControl from "@material-ui/core/FormControl";
import Paper from "@material-ui/core/Paper";
import Input from "@material-ui/core/Input";
import InputLabel from "@material-ui/core/InputLabel";
import Button from "@material-ui/core/Button";
import IconButton from '@material-ui/core/IconButton'
import Slide from "@material-ui/core/Slide";
import Gavel from "@material-ui/icons/Gavel";
import VerifiedUserTwoTone from "@material-ui/icons/VerifiedUserTwoTone";
import To from '../Shared/to'
import { Dialog, DialogTitle, DialogContent, DialogContentText, DialogActions, Checkbox } from "@material-ui/core";
import Policy from './policy'
import {InputAdornment} from '@material-ui/core'
import {Error} from '@material-ui/icons'
import Logo from '../../assets/logo.png'
import Grid from '@material-ui/core/Grid'
import Container from '@material-ui/core/Container'

import ArrowRight from '@material-ui/icons/ArrowRight'
import Check from '@material-ui/icons/Check'

function Transition(props) {
  return <Slide direction="up" {...props} />;
}

const Register = ({ classes, setCurrentRoute }) => {
  const [username, setUsername] = useState("")
  const [email, setEmail] = useState("")
  const [password, setPassword] = useState("")
  const [cpassword, setCpassword]=useState("")
  const [passwordNotMatch, setPasswordNotMatch] = useState("")
    const [open, setOpen] = useState(false)
    const [passwordValid, setPasswordValid] = useState(true)
  const [showPolicy, setShowPolicy] = useState(false)
  const [policyAccepted, setPolicyAccepted] = useState(false)

  const handleSubmit = async (event, createUser, client) => {
      event.preventDefault()
    //
    if (!passwordNotMatch && passwordValid) {
      const res = await  createUser()
      console.log(res)
       localStorage.setItem("authtoken", res.data.createUser.token)
       setCurrentRoute("Email")
      console.log(localStorage.getItem("authtoken")) 
    }
  
    
 
  }
  return (
    <Container maxWidth="md">
      
    <Grid  container spacing={3}>
      <Grid item md={6} sm={12}>
      <div    className={classes.paper}>
       
      <img  src={Logo} className={classes.avatar} />
       </div>
       <div>
       <Typography 
                      
                       variant="body2"
                      
                       >
                          <IconButton edge="end" aria-label="Delete">
                     <Check style={{    background:"orange",
    color: "indigo",
    borderRadius: "50%", marginRight:"16px"}} />
                    </IconButton>
                    Lorem Ipsum is simply dummy text of the printing and typesetting industry. Lorem Ipsum has been the industry's standard dummy text</Typography>
                       <Typography 
                       style={{marginTop:"16px"}}
                      
                      variant="body2"
                     
                      >
                         <IconButton edge="end" aria-label="Delete">
                    <Check style={{    background:"orange",
   color: "indigo",
   borderRadius: "50%", marginRight:"16px"}} />
                   </IconButton>
                   Lorem Ipsum is simply dummy text of the printing and typesetting industry. Lorem Ipsum has been the industry's standard dummy text</Typography>
                      <Typography 
                       style={{marginTop:"16px"}}
                      variant="body2"
                     
                      >
                         <IconButton edge="end" aria-label="Delete">
                    <Check style={{    background:"orange",
   color: "indigo",
   borderRadius: "50%", marginRight:"16px"}} />
                   </IconButton>
                   Lorem Ipsum is simply dummy text of the printing and typesetting industry. Lorem Ipsum has been the industry's standard dummy text</Typography>
             
                        
       </div>
      
       
      </Grid>
      <Grid item md={6} sm={12}>

      <div >
      <Paper className={classes.paper}>
    
        <Typography variant="h4" component={"h4"}>Create an Account</Typography>

       <Mutation mutation={REGISTER_MUTATION}
    variables={{username, email,password}}
    onCompleted={(data) => {
        console.log(data)
        setOpen(true)
    }}
>

        {(createUser, {loading, error, called, client})=> {
       let message
       if (error) {
        message=error.message.replace("GraphQL Error:", "").trim()
          message=message.replace("GraphQL error:", "").trim()
          
       }    return (
              <div>
{error && (
  
<div className={classes.text}>
                    <Typography 
                        className={classes.text}
                       variant="body2"
                       color="error"
                       >
                       {message}</Typography>
             
                        </div>
)}
             

                <form
              onSubmit = {event => handleSubmit(event, createUser, client)}
                className={classes.form}
              >
                <FormControl margin="normal" required fullWidth>
                  <InputLabel htmlFor="username">Username</InputLabel>
                  <Input
                    id="username"
                  onChange={(event) =>setUsername(event.target.value)}
                  />
                </FormControl>
                <FormControl margin="normal" required fullWidth>
                  <InputLabel htmlFor="email">Email</InputLabel>
                  <Input
                    id="email"
                    type="email"
                  
                    onChange={(event) =>setEmail(event.target.value)}
                   
                  />
                </FormControl>
                
                <FormControl margin="normal" required fullWidth>
                  <InputLabel htmlFor="password">Password</InputLabel>
                  <Input
                    id="password"
                    type="password"
                    helperText="Some important text"
                    endAdornment={
                      !passwordValid ? (
                      <InputAdornment position="end">
                          <Error style={{color:"red"}} />
                      </InputAdornment>
                      ) : null
                    }
                    
                    onChange={(event) =>{
                     
                      setPassword(event.target.value)
                     
                    }}
                    onBlur = {()=> {
                      let regex= new RegExp("^((?=.*[a-z])(?=.*[A-Z])(?=.*[0-9])(?=.{8,}))");
                      if (!regex.test(password))
                        setPasswordValid(false)
                        else
                        setPasswordValid(true)
                    }}
                  />

                </FormControl>
                {!passwordValid &&    <p style={{marginTop: 0, opacity:"0.7"}}>Minimum 8 Characters, Must Contain one Capital, one small and one number</p>}
                
                <FormControl margin="normal" required fullWidth>
                  <InputLabel htmlFor="password">Confirm Password</InputLabel>
                  <Input
                    id="password"
                    type="password"
                    endAdornment={
                      passwordNotMatch ? (
                      <InputAdornment position="end">
                          <Error style={{color:"red"}} />
                      </InputAdornment>
                      ) : null
                    }
                    onBlur={()=>{
                    if ( password != cpassword) 
                     setPasswordNotMatch(true)
                     else
                     setPasswordNotMatch(false) 
                    }}
                    onChange={(event) => {
                      setCpassword(event.target.value)
                      if (event.target.value==password)
                      setPasswordNotMatch(false) 

                    }
                    }
                  />
                   {passwordNotMatch &&    <p style={{marginTop: 0, opacity:"0.7"}}>Password Does Not Match</p>}
                </FormControl>
                    <div style={{display:"flex", flexDirection:"row", alignItems:"center"}}>
                <Checkbox 
                onChange={()=>setPolicyAccepted(!policyAccepted)}
                />
                <Typography onClick={()=>setShowPolicy(true)} variant="headline" style={{color:"indigo"}}>Accept User Account policy</Typography>
                </div>
                <Button
                  type="submit"
                  fullWidth
                  variant="contained"
                  color="secondary"
                 disabled={loading || !username.trim() || !password.trim() || !email.trim() || !policyAccepted || !cpassword.trim() }
                  className={classes.submit}
                >
                {loading ? "Registering ..." : "Register"}
                </Button>
                <Typography 
                      
                      
                      className={classes.loginbutton}
                      onClick={() => setCurrentRoute("Login")}
                       variant="body2"
                      style={{color:"indigo"}}
                       >
                     Already have an account ? Log In
       
                  
                  </Typography>
                 
                <Button
                  onClick={() => client.writeData({data:{tempuser:true}})}
                  color="primary"
                  variant="outlined"
                  fullWidth
                >
                  Skip Registrayion
                </Button>
             
               
              </form>
              </div>
            )
        }}

       </Mutation>
           
       
      </Paper>

      {showPolicy && <Policy handleClose={()=>setShowPolicy(false)} /> }
<Dialog 
open={open}

TransitionComponent={Transition}

>
        <DialogTitle>
            <VerifiedUserTwoTone className={classes.icon} />
                New Account
        
        </DialogTitle>
    <DialogContent>
        <DialogContentText>User Successfully Registered</DialogContentText>
        </DialogContent>
        <DialogActions>
            <Button 
           color="primary"
           variant="contained"
           onClick={()=> {
            setOpen(false) 
            setCurrentRoute("Login")}

           }
            >
                Login
                </Button>
        </DialogActions>
    </Dialog>
      {/* Success Dialog */}
    
    </div>

      </Grid>
      
    </Grid>
    </Container>
   
  );
};

const REGISTER_MUTATION = gql`
  mutation($username: String!, $email: String!, $password: String!) {
    createUser(username: $username, email: $email, password: $password) {
      token
    }
  }
`;

const styles = theme => ({
  root: {
    width: "auto",
    display: "block",
    marginLeft: theme.spacing.unit * 3,
    marginRight: theme.spacing.unit * 3,
    [theme.breakpoints.up("md")]: {
      width: 400,
      marginLeft: "auto",
      marginRight: "auto"
    }
  },
  paper: {
    marginTop: theme.spacing.unit * 8,
    display: "flex",
    flexDirection: "column",
    alignItems: "center",
    padding: theme.spacing.unit * 2
  },
  title: {
    marginTop: theme.spacing.unit * 2,
    color: theme.palette.openTitle
  },
  avatar: {
    width: "96px",
    borderRadius:" 50%"
  },
  form: {
    width: "100%",
    marginTop: theme.spacing.unit
  },
  submit: {
    marginTop: theme.spacing.unit * 2,
    marginBottom: theme.spacing.unit * 2
  },
  icon: {
    padding: "0px 2px 2px 0px",
    verticalAlign: "middle",
    color: "green"
  },
  text:{
    marginTop: theme.spacing.unit*3
  },
  loginbutton:{
    marginBottom: theme.spacing.unit * 2,
    "&:hover":{
      cursor:"pointer"
    }
  }
});

export default withStyles(styles)(Register);





