
import React from "react";
import { MuiThemeProvider, createMuiTheme } from "@material-ui/core/styles";
import indigo from "@material-ui/core/colors/indigo";
import orange from "@material-ui/core/colors/orange";
import red from "@material-ui/core/colors/red";
import CssBaseline from "@material-ui/core/CssBaseline";
import { Query } from "react-apollo";
import { gql } from "apollo-boost";
import Auth from './components/Auth'
import Feed from './components/Feed'
import './App.css'
import {Context} from './components/usercontext'
import View from './components/view'
import Errorshow from './components/Shared/NetworkError'


const IS_LOGGED_IN_QUERY = gql`
{
  isLoggedIn, tempuser @client
}

`

// 
const Tweets = () => (
 
    <Query query={IS_LOGGED_IN_QUERY}>
      {({data}) => {
        return (
          <div>
            {data.tempuser ? (
             
   
                   <Context.Provider
           value={false}
         >
           <Feed />
         </Context.Provider>
         
         
            ): (
              data.isLoggedIn ? (
            

                <Query query={GET_CURRENT_USER}>
                {({ loading, error, data }) => {
                   
                  if (loading) return "Loading...";
                 if (error) return <Errorshow error={error} />
            
                  return (
    
                    <Context.Provider
            value={data.me}
          >
            <Feed currentUser={data.me} />
          </Context.Provider>
          
              
                  )
                }
              }
              </Query>
                
                ) : <Auth />
            )}
          
          </div>
        )  
    }}
 

    </Query>
  )

export const GET_CURRENT_USER=gql`
{
  me {
    id
    username
    profileSet {
      profilePic
      emailverified
      
    }
  }
}
`




// A theme with custom primary and secondary color.
// It's optional.
const theme = createMuiTheme({
  palette: {
    primary: {
      light: indigo[500],
      main: indigo[700],
      dark: indigo[900]
    },
    secondary: {
      light: orange[300],
      main: orange[500],
      dark: orange[700]
    },
    error:{
      light:red[300],
      main:red[300],
      dark:red[700]
    }
  },
  typography: {
    useNextVariants: true
  }
});

function withRoot(Component) {
  function WithRoot(props) {
    // MuiThemeProvider makes the theme available down the React tree
    // thanks to React context.
    return (
      <MuiThemeProvider theme={theme}>
        {/* CssBaseline kickstart an elegant, consistent, and simple baseline to build upon. */}
        {/* https://material-ui.com/getting-started/usage/#cssbaseline */}
        <CssBaseline />
        <Component {...props} />
      </MuiThemeProvider>
    );
  }

  return WithRoot;
}

export default withRoot(Tweets);
