import React , {useState}from 'react'
import Register from './register'
import Login from './login'
import TwoFA from './2fa'
import Email from './email'
import { gql } from "apollo-boost";
import { Query } from "react-apollo";
import ForgetPassword from './forgetPassword'
const Auth = () => {
    const [currentRoute, setCurrentRoute] = useState("Register")
    const [username, setUsername] = useState("")
    return (
        <div>



 
    <Query query={IS_LOGGED_IN_QUERY}>
      {({data}) => {
          if (data.authRoute)
          setCurrentRoute(data.authRoute)
          return (
              <Query query={GENERATE_OTP}>

{({ loading, error, data }) => (
<div>
          
          {currentRoute == "Register" ? <Register setCurrentRoute={setCurrentRoute} /> : null}

          {currentRoute == "Email" ? <Email setCurrentRoute={setCurrentRoute} /> : null}
          
          
          {currentRoute == "Login" ? <Login 
          setPropsUsername={setUsername}
          setCurrentRoute={setCurrentRoute} /> : null}

          {currentRoute == "2fa" ? <TwoFA 
          username={username}
          setCurrentRoute={setCurrentRoute} /> : null}
          {currentRoute == "ForgetPassword" ? <ForgetPassword /> : null}
          </div>
)}



                </Query>  
            
          )
      }

    }
    </Query>
        </div>
    )
}




const IS_LOGGED_IN_QUERY = gql`
{
  authRoute @client
}
`

const GENERATE_OTP=gql`
{
    generateotp
}
`

export default Auth;