import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import clsx from 'clsx';
import { Mutation } from "react-apollo";
import Card from '@material-ui/core/Card';
import CardHeader from '@material-ui/core/CardHeader';
import CardMedia from '@material-ui/core/CardMedia';
import CardContent from '@material-ui/core/CardContent';
import CardActions from '@material-ui/core/CardActions';
import Collapse from '@material-ui/core/Collapse';
import Avatar from '@material-ui/core/Avatar';
import IconButton from '@material-ui/core/IconButton';
import Typography from '@material-ui/core/Typography';
import { red } from '@material-ui/core/colors';

import ShareIcon from '@material-ui/icons/Share';
import ExpandMoreIcon from '@material-ui/icons/ExpandMore';
import Comment from '@material-ui/icons/Comment'
import MoreVertIcon from '@material-ui/icons/MoreVert';
import Likes from '../../Shared/likeshow'
import Comments from '../../Shared/commentshow'
import Commentshow from '../../Shared/commentshow';
import Createlike from '../../Shared/createLike'
import CircularProgress from '@material-ui/core/CircularProgress';
import ReactCountryFlag from "react-country-flag";
import {gql} from 'apollo-boost'
import { STATIC_URL } from '../../../config';
import Divider from '@material-ui/core/Divider'
import {withRouter} from 'react-router-dom'

const useStyles = makeStyles(theme => ({
 card: {
   marginBottom:"10px"
 },
 
  expand: {
    transform: 'rotate(0deg)',
    marginLeft: 'auto',
    transition: theme.transitions.create('transform', {
      duration: theme.transitions.duration.shortest,
    }),
  },
  expandOpen: {
    transform: 'rotate(180deg)',
  },
  avatar: {
    backgroundColor: red[500],
  },
  progress : {
    height:25,
    width:25
  },
  avatarimage:{
    width:"100%"
  },
  showpointer:{
    cursor:"pointer"
  }

}));

 function RecipeReviewCard(props) {
  const classes = useStyles();
 
  const [expanded, setExpanded] = React.useState(false);
  const [menuExpande, setMenuExpanded] = React.useState(false)
  const [loading, setLoading] = React.useState(false)


const handleNavigation = (id) => {
  props.history.push(`/profile/${id}`)
}

  const handleDelete = async (deleteTweet) => {
  
    const res = await deleteTweet()
   
  }

  function handleExpandClick(expandedItem) {
    if (expanded == "likes" && expandedItem == "likes")
    setExpanded(false)
    else if (expanded == "comments" && expandedItem=="comments") {
      setExpanded(false)

    }
    else 
    setExpanded(expandedItem)
    
  }
  let {item} = props
  let tweettime = item.tweettime.split("T")[0]
 let  commentCount = item.comments.length
console.log(item)
  return (
    <Mutation mutation={DELETE_MUTATION}
    variables={{tweetid:props.item.id}}
    onCompleted={data => {
      
    }}
    refetchQueries={() =>{
     
      return [{ query: props.refetchQuery, variables:{username:props.username} }]
     
    }} 
    
>

        {(deleteTweet, {loading, error})=> {
          if (loading) {
            return (
              <div style={{display:"flex", justifyContent:"center",alignItems:"center"}}>
             <CircularProgress className={classes.progress} 
          style={{height:"25px", width:"25px"}}
          color="secondary" /> 
          </div>
            )
          }
          if (error)
          alert(error.message)
        
          return (
    <Card className={classes.card}>
      <CardHeader
      onClick={()=> handleNavigation(item.id)}
      className={classes.showpointer}
        avatar={
          <Avatar>
           <img className={clsx(classes.showpointer, classes.avatarimage)} src={`${STATIC_URL}${item.user.profileSet[0].profilePic}`} />
          </Avatar>
        }
        action={
          <div style={{position:"relative"}}>
          <ReactCountryFlag code={item.tweetcountry} svg />
          <IconButton 
          onClick={(e)=>{
            e.stopPropagation()
            setMenuExpanded(!menuExpande)

          }
          }
          aria-label="Settings">
            <MoreVertIcon />
         
          
          </IconButton>
          {menuExpande && (
            <div style={{zIndex:999, background:"white",position:"absolute",bottom:"-90px", right:"30px",    border: "1px solid indigo",
          }}>
            <Typography 
            style={{  padding: "5px 90px"}}
            variant="body2" color="textSecondary" component="p">

            
              
          Report Abuse
        </Typography>
        <Divider />
        <Typography  
        onClick={(e)=>handleDelete(deleteTweet)}
        style={{  padding: "5px 90px"}} variant="body2" color="textSecondary" component="p">

            
              
Delete Tweet
</Typography>

            </div>
            
            )}
          </div>
        }
        title={item.user.username}
        subheader={tweettime}
      />
      <CardMedia
       style={item.tweetfile ?  {height: 0,paddingTop: '56.25%'} : null}
        image={`${STATIC_URL}${item.tweetfile}`}
        title="Paella dish"
      />
      <CardContent>
        <Typography variant="body2" color="textSecondary" component="p">
          {item.tweettext}
        </Typography>
      </CardContent>
      <CardActions disableSpacing>
         <Createlike 
         page={props.page}
         username={props.username}
         refetchQuery={props.refetchQuery}
         tweetid={item.id}
         likes={item.likes}
         CREATE_LIKE_MUTATION={CREATE_LIKE_MUTATION}
         setLoading={(isLoading) => setLoading(isLoading)}
         expanded={expanded}
         setExpanded={ handleExpandClick} item={item} />
        <span>
          {commentCount}
        <IconButton 
          className={clsx(classes.expand, {
            [classes.expandOpen]: expanded=="comments",
          })}
          onClick={() => handleExpandClick("comments")}
          aria-expanded={expanded}
          aria-label="Show more"
        style={{paddingLeft:"5px"}} aria-label="Share">
         <Comment />
        </IconButton>
        </span>
        <IconButton
          className={clsx(classes.expand, {
            [classes.expandOpen]: expanded,
          })}
          onClick={handleExpandClick}
          aria-expanded={expanded}
          aria-label="Show more"
        >
          <ExpandMoreIcon />
        </IconButton>
      </CardActions>
      <Collapse 
      style={{overflow:"auto"}}
      in={expanded} timeout="auto" unmountOnExit>
        <CardContent>
          {loading ?(
          <div style={{display:"flex", justifyContent:"center",alignItems:"center"}}>
             <CircularProgress className={classes.progress} 
          style={{height:"25px", width:"25px"}}
          color="secondary" /> 
          </div>
          )
          : (
              expanded == "likes" && <Likes 
              refetchQuery={props.refetchQuery}
              likes={item.likes}
              item={item}
              />
          )}
        
         {expanded == "comments" && <Comments  
         tweetid={item.id}
         page={props.page}
         comments={item.comments}
         setLoading={(isLoading) => setLoading(isLoading)}
         CREATE_COMMENT_MUTATION={CREATE_COMMENT_MUTATION}
         refetchQuery={props.refetchQuery}
         username={props.username}
         item={item}/>}
        </CardContent>
      </Collapse>
    </Card>
          )
        }
      }
        </Mutation>
  );
}



const CREATE_LIKE_MUTATION = gql`
  mutation($tweetid: Int!) {
    createLike(tweetId: $tweetid) {
     
      tweet {
          id
      }
      likedordisliked
    }
  }
`;


const CREATE_COMMENT_MUTATION = gql`
  mutation($tweetid: Int!,$commenttext:String!) {
    createComment(tweetId: $tweetid,commenttext:$commenttext ) {
     tweet {
       id
     }
    }
  }
`;


const DELETE_MUTATION = gql`
mutation($tweetid:Int!){
  deleteTweet(tweetid:$tweetid) {
    status
  }
}
`
export default withRouter(RecipeReviewCard)