const ImgIcon = () => (
  <svg focusable="false" viewBox="0 0 24 24" xmlns="http://www.w3.org/2000/svg">
    <path d="M0 0h24v24H0z" fill="none" />
    <path d="M14 13l4 5H6l4-4 1.79 1.78L14 13zm-6.01-2.99A2 2 0 0 0 8 6a2 2 0 0 0-.01 4.01zM22 5v14a3 3 0 0 1-3 2.99H5c-1.64 0-3-1.36-3-3V5c0-1.64 1.36-3 3-3h14c1.65 0 3 1.36 3 3zm-2.01 0a1 1 0 0 0-1-1H5a1 1 0 0 0-1 1v14a1 1 0 0 0 1 1h7v-.01h7a1 1 0 0 0 1-1V5z" />
  </svg>
);

const FileInput = ({ onChange, children }) => {
  const fileRef = useRef();
  const onPickFile = event => {
    onChange([...event.target.files]);
  };
  return (
    <div
      style={{
        width: "35px",
        height: "35px",
        borderRadius: "3px"
      }}
      onClick={() => fileRef.current.click()}
    >
      {children}
      <input
        multiple
        ref={fileRef}
        onChange={onPickFile}
        type="file"
        style={{ visibility: "hidden" }}
      />
    </div>
  );
};

const Img = ({ file, onRemove, index, isSingle }) => {
  const [fileUrl, setFileUrl] = useState(null);
  useEffect(() => {
    if (file) {
      setFileUrl(URL.createObjectURL(file));
    }
  }, [file]);

  const dimensionStyle = isSingle
    ? {
        maxWidth: "100%",
        maxHeight: "auto"
      }
    : {
        maxWidth: "230px",
        maxHeight: "95px"
      };

  return fileUrl ? (
    onRemove ? (
      <div
        style={{ position: "relative", maxWidth: "230px", maxHeight: "95px" }}
      >
        <img
          style={{
            ...dimensionStyle,
            display: "block",
            width: "auto",
            height: "auto"
          }}
          alt="pic"
          src={fileUrl}
        />
        <div
          onClick={() => onRemove(index)}
          style={{
            position: "absolute",
            right: 0,
            top: 0,
            width: "20px",
            height: "20px",
            borderRadius: "50%",
            background: "black",
            color: "white",
            display: "flex",
            alignItems: "center",
            justifyContent: "center"
          }}
        >
          x
        </div>
      </div>
    ) : (
      <img
        style={{
          ...dimensionStyle,
          display: "block",
          width: "auto",
          height: "auto"
        }}
        alt="pic"
        src={fileUrl}
      />
    )
  ) : null;
};

Img.defaultProps = {
  isSingle: false
};

