const Tweet = ({ tweet: { text, images } }) => (
  <div
    style={{
      margin: "20px",
      border: "1px solid grey",
      width: "600px",
      padding: "20px",
      borderRadius: "3px"
    }}
  >
    <div>{text}</div>
    {images.length > 0 && (
      <div
        style={{
          display: "flex",
          flexDirection: "row",
          flexWrap: "wrap",
          background: "fbfbfb",
          padding: "30px 0"
        }}
      >
        {images.map((img, i) => (
          <Img key={i} file={img} index={i} isSingle={images.length === 1} />
        ))}
      </div> 
    )}
  </div>
);

function TweetSheet(props) {
  const [text, setText] = useState("");
  const [pics, setPics] = useState([]);
  const textAreaRef = useRef();
  const [tweets, setTweets] = useState([]); // array of object of shape {text: '', images: []}
  const [isAbusive, setIsAbusive]=useState(false)
  const [componentLoading, setComponentLoading] = React.useState(false)
  const [componentError, setComponentError]=React.useState(false)
  const insertAtPos = value => {
    const { current: taRef } = textAreaRef;
    let startPos = taRef.selectionStart;
    let endPos = taRef.selectionEnd;
    setText(
      taRef.value.substring(0, startPos) +
        value.native +
        taRef.value.substring(endPos, taRef.value.length)
    );
  };
  const onClickTweet =async (createTweet) => {
    setComponentLoading(true)
    let res;
    let tempIsAbusive=false
    
    if (text) {
      const abusiveRes = await Axios.post(PERSPECTIVE_API_URL, {
        comment: {
          text: text
        },
        languages: ["en"],
        requestedAttributes: {
          TOXICITY: {},
          INSULT: {},
          FLIRTATION: {},
          THREAT: {}
        }
      })      
      const threat = abusiveRes.data.attributeScores.THREAT.summaryScore.value;
        const insult = abusiveRes.data.attributeScores.INSULT.summaryScore.value;
        const toxic = abusiveRes.data.attributeScores.TOXICITY.summaryScore.value;

            
       if ((threat >0.5) || (insult > 0.5) || (toxic > 0.5)) {
        
        setIsAbusive(true)
        tempIsAbusive=true
        setComponentLoading(false)
        
        setComponentError({message:"ABUSIVE LANGUAGE : Your tweet contains abusive language"})
        
       }         
        
   
      if (tempIsAbusive == false)
      {

        if (pics.length>0) {
            if (pics[0].size / 1048576 <= 5) {
                let a=new Date().toString().split("(")[0]
                let b= a.split(" ")
                b.pop()
                b.pop()
                let picname= b.join("-")+pics[0].name
              
                let data=new FormData()
                data.append("token", localStorage.getItem("authtoken"))
                data.append("name", picname)
                data.append("file",  pics[0])
                Axios.post(UPLOAD_URL, data)
                .then(res => {
                  console.log(res)
                    if (res.data.success == true) {
                        createTweet({ variables: { tweettext: text, tweetimage:picname } });
                        setText("")
                        setPics([])
                    }
                    else {
                      setComponentLoading(false)
                      setComponentError({message:"Some Error occured on server"})
                    }
                })
                .catch(err => {
                  setComponentLoading(false)
                  setComponentError({message:"Network Error"})
                })
            }
            else {
              setComponentLoading(false)
              setComponentError({message:"file size should be less then 5mb"})
            }
        }
    else {
        res = await createTweet({ variables: { tweettext: text, tweetimage:"null" } });
        setText("")
        setComponentLoading(false)
        setComponentError(false)
      
    } 
  }
}



  else {
      setComponentLoading(false)
      setComponentError({message:"Please Use Words , They are free"})
  }
   
};
  return (
    <>
        <Mutation mutation={CREATE_TWEET}
          refetchQueries={() =>{
          
            return [{ query: GET_TWEETS, variables:{username:props.username}}]
          }} 

    
>

        {(createTweet, {loading, error, called, client})=> {
          if (loading || componentLoading) return (
            <div style={{height:"200px", display:"flex", flexDirection:"column", alignItems:"center", justifyContent:"center"}}>
            <Loading />
            </div>
          )
       
         
            return (
      <div
        style={{
          display: "flex",
          flexDirection: "column",
        
          width: "100%",
         
          minHeight: "200px",
          padding: "20px"
        }}
      >
      {componentError &&  <Error error={componentError} /> }
        <div
          style={{
            display: "flex",
            flexDirection: "column",
            flex: 1,
           border:"1px solid indigo",
            margin: "0px"
          }}
        >
          {(error || isAbusive) && <SnackBarError error={error ? error : { message:"ABUSIVE LANGUAGE : Your tweet contains abusive language"}} />}
          <textarea
            ref={textAreaRef}
            placeholder="Let Your Thoughts Flow Out"
            value={text}
            style={{ flex: 1, border: "none", minHeight: "150px" }}
            onChange={e =>text.length<=300 ? setText(e.target.value): false}
          />
          <div
            style={{
              display: "flex",
              flexDirection: "row",
              flexWrap: "wrap",
              background: "fbfbfb"
            }}
          >
            {pics.map((picFile, index) => (
              <Img
                key={index}
                index={index}
                file={picFile}
                onRemove={rmIndx =>
                  setPics(pics.filter((pic, index) => index !== rmIndx))
                }
              />
            ))}
          </div>
        </div>
        <div
          style={{
            display: "flex",
            flexDirection: "row",
            alignItems: "center",
            border:"1px solid indigo",
            padding:"5px"
          }}
        >
          <div style={{ marginRight: "20px" }}>
            <FileInput onChange={pics => setPics(pics)}>
              <ImgIcon />
            </FileInput>
          </div>
          <EmojiPicker onSelect={insertAtPos} />
          <div
            style={{
              flex: 1,
              display: "flex",
              flexDirection: "row",
              alignItems: "center",
              justifyContent: "flex-end"
            }}
          >
              <Button 
              onClick={()=>onClickTweet(createTweet)}
              variant="outlined" color="primary" >
                Tweet
                </Button>
    
          </div>
        </div>
      </div>
            )
          }
        }
        </Mutation>
     
    </>
  );
}

function Tweetbox(props) {
  return (
    <div
      style={{
        display: "flex",
        flex: 1,
        height: "100%",
        width: "100%",
        flexDirection: "column",
        alignItems: "center",
       
      }}
    >
      <TweetSheet username={props.username} />
    </div>
  );
}


const CREATE_TWEET=gql`
mutation($tweettext:String!, $tweetimage:String!) {
  createTweet(tweetext:$tweettext, tweetimage:$tweetimage) {
    tweet {
      id
      tweettext
      tweetfile
    }
  }
}
`

export default Tweetbox