import React, { useState } from "react";
import { Mutation, Query } from "react-apollo";
import { gql } from "apollo-boost";
import withStyles from "@material-ui/core/styles/withStyles";
import Typography from "@material-ui/core/Typography";
import Avatar from "@material-ui/core/Avatar";
import FormControl from "@material-ui/core/FormControl";
import Paper from "@material-ui/core/Paper";
import Input from "@material-ui/core/Input";
import InputLabel from "@material-ui/core/InputLabel";
import Button from "@material-ui/core/Button";
import Grid from '@material-ui/core/Grid';
import Slide from "@material-ui/core/Slide";
import Gavel from "@material-ui/icons/Gavel";
import VerifiedUserTwoTone from "@material-ui/icons/VerifiedUserTwoTone";
import InfiniteScroll from 'react-infinite-scroller';
import { Dialog, DialogTitle, DialogContent, DialogContentText, DialogActions } from "@material-ui/core";
import Singletweet from './singletweet'
import {MyInstagramLoader} from '../../Shared/initialLoad'


const Tweetsheet = ({ classes, setCurrentRoute, username, profile }) => {
  
  const [hasMore, setHasMore] =React.useState(true)
  const [page, setPage] = React.useState(1)
  return (
    <div className={classes.root}>
     

     <Query query={GET_TWEETS}
     variables={{username:username, page}}
     >
    {({ loading, error, data, fetchMore }) => {
       
      if (loading) return <MyInstagramLoader />
      if (error) return `Error! ${error.message}`;

      return (
       <div >

<Grid  container spacing={3}>
       
       <Grid style={{background:"#eeeeee"}} item xs={12} md={4}>
           <div >
       <Typography 
        style={
          {
                border: "1px solid indigo",
                paddingLeft: "10px",
                borderRightRadius: "50%",
                borderBottomRightRadius: "1em",
                borderTopRightRadius: "1em", marginBottom:"16px", fontWeight:"initial", fontSize:"1rem",
                background:"indigo", color:"white"
            }  
           }
           variant="h6" 
          
           component="p">
                  Your Tweets
                </Typography>
    </div>

 
   
           </Grid>



           <Grid item xs={12} md={8}>

         
{data.tweets.map(item => <Singletweet 
           username={username}
            refetchQuery={GET_TWEETS}
            profilePic={profile.profilePic}
           item={item} />)}


         
           </Grid>
           </Grid>
       </div>
      );
    }}
  </Query>
       
 
  
    
    </div>
  );
};

export const GET_TWEETS = gql`
query($username:String){
    tweets (username:$username) {
      id
        user {
            username
           
        }
      tweettext
      tweetcountry
      tweetfile
      tweettime
      likes {
        id
        user {
          username
          profileSet {
            profilePic
          }
        }
      }
      comments {
        id
        user {
          username
          profileSet {
            profilePic
          }
        }
        comment
      }
    }
  }
`;

const styles = theme => ({
  root: {
    padding:20
  },
});

export default withStyles(styles)(Tweetsheet);
