function Password() {
    const [password, setPassword]=useState("")
    const [opassword, setOpassword]=useState("")
    const [cpassword, setCpassword]=useState("")
    const [passwordNotMatch, setPasswordNotMatch] = useState("")
  
    const [passwordValid, setPasswordValid] = useState(true)
   
    
   const handleSubmit = async (updatePassword) => {
  
  //
  if (!passwordNotMatch && passwordValid) {
    const res = await  updatePassword()
 
    if (res.data.updatePassword.status)
    alert("Pasword Updated Succesfully")
    
  }

  

} 
    return (
        <>


<Mutation mutation={UPDATE_PASSWORD_MUTATION}
    variables={{op:opassword, p:password}}
    onCompleted={(data) => {
       
       
    }}
>

        {(updatePassword, {loading, error, called, client})=> {
            return (
              <div>


        <div style={{    borderBottom: "1px solid #ddd",
        paddingBottom: "10px",
        paddingLeft: "20px"}}>
               <Typography variant="h6"  component="h6">
                      Password Change
                    </Typography>
</div>

{error && (
  
    <div style={{marginTop:"24px"}}>
                        <Typography 
                            style={{marginTop:"24px"}}
                           variant="body2"
                           color="error"
                           >
                           {error.message}</Typography>
                 
                            </div>
    )}
                 
    
                    <FormControl margin="normal" required fullWidth>
                  <InputLabel htmlFor="password">Old Password</InputLabel>
                  <Input
                    id="password"
                    type="password"
                    value={opassword}
                    onChange={(event) =>setOpassword(event.target.value)}
                  />
                </FormControl>
                <FormControl margin="normal" required fullWidth>
                  <InputLabel htmlFor="password">Old Password</InputLabel>
                  <Input
                    id="password"
                    type="password"
                    endAdornment={
                      !passwordValid ? (
                      <InputAdornment position="end">
                          <Error style={{color:"red"}} />
                      </InputAdornment>
                      ) : null
                    }
                    onChange={(event) =>{
                     
                      setPassword(event.target.value)
                     
                    }}
                    onBlur = {()=> {
                      let regex= new RegExp("^((?=.*[a-z])(?=.*[A-Z])(?=.*[0-9])(?=.{8,}))");
                      if (!regex.test(password))
                        setPasswordValid(false)
                        else
                        setPasswordValid(true)
                    }}
                  />
                </FormControl>
                <FormControl margin="normal" required fullWidth>
                  <InputLabel htmlFor="password">Old Password</InputLabel>
                  <Input
                    id="password"
                    type="password"
                    endAdornment={
                      passwordNotMatch ? (
                      <InputAdornment position="end">
                          <Error style={{color:"red"}} />
                      </InputAdornment>
                      ) : null
                    }
                    onBlur={()=>{
                    if ( password != cpassword) 
                     setPasswordNotMatch(true)
                     else
                     setPasswordNotMatch(false) 
                    }}
                    onChange={(event) =>setCpassword(event.target.value)}
                  />
                </FormControl>
              
                <Button
                onClick={()=>handleSubmit(updatePassword)}
                 style={{marginTop:"16px"}}
                 fullWidth
                  variant="contained"
                  color="primary"
                 disabled={loading || !password.trim() || !cpassword.trim()
                    || !opassword.trim() }
                 
                
               >
               
                {loading==false ? "Update" : "Updating"}
                </Button>
                </div>
              
            )
            }
            }
            </Mutation>
            
</>
    )
}

function Twofactor({refetchQueries, profile, cuser}) {
   
    const handleUpdate =async (updateTwofa, value) => {
        let res=await updateTwofa({variables:{value:value}})
      
    }
    return (

<Mutation mutation={UPDATE_TWO_FA}
  refetchQueries={refetchQueries}
    onCompleted={(data) => {
     
       
    }}
>

        {(updateTwofa, {loading, error, called, client})=> {
            return (

        <div>

        
        <div style={{    borderBottom: "1px solid #ddd",
        paddingBottom: "10px",
        paddingLeft: "20px"}}>
               <Typography variant="h6"  component="h6">
                     Two Factor Authentication
                    </Typography>
</div>

<div  style={{ padding:"16px"}}>

{profile.is2fa ? (
    <div style={{display:"flex", justifyContent:"space-between", alignItems:"center"}}>
<Typography variant="body1"  component="h6">
                     Two Factor Authentication is enabled.
        
                    </Typography>
                    <Button variant="outlined" 
                    onClick={()=>handleUpdate(updateTwofa, false)}
                    disabled={loading}
                    color="primary" >
        {loading ? "Disabling ..." : "Disable"}
      </Button>
        </div>

) : (
    <div style={{display:"flex", justifyContent:"space-between", alignItems:"center"}}>
<Typography variant="body1"  component="h6">
                     Two Factor Authentication is disabled
        
                    </Typography>
                    <Button variant="outlined"
                     onClick={()=>handleUpdate(updateTwofa, true)}
                    disabled={loading}
                    color="primary" >
         {loading ? "Enabling ..." : "Enable"}
      </Button>
        </div>

)}

</div>

</div>
            )
}
        }
        </Mutation>
    )
}



const UPDATE_PASSWORD_MUTATION=gql`
mutation($op:String!, $p:String) {
    updatePassword(oldpassword:$op, password:$p) {
        status
    }
}

`

const UPDATE_TWO_FA=gql`
mutation($value:Boolean!) {
    updateTwofa(value:$value) {
        status
    }
}

`