import React from 'react'
import Createcomment from './createComment'
import { makeStyles } from '@material-ui/core/styles';
import ListItem from '@material-ui/core/ListItem';
import ListItemText from '@material-ui/core/ListItemText';
import ListItemAvatar from '@material-ui/core/ListItemAvatar';
import { FixedSizeList } from 'react-window';
import Avatar from '@material-ui/core/Avatar';
import avatar from '../../assets/avatar.jpeg'
import PropTypes from 'prop-types';
import {STATIC_URL} from '../../config'
import {Context} from '../usercontext'
const useStyles = makeStyles(theme => ({
    root: {
      width: '100%',
      height: 400,
      maxWidth: 360,
      backgroundColor: theme.palette.background.paper,
    },
    showless: {
      width:"100%",
      mexWidth:360,
      backgroundColor: theme.palette.background.paper,
    }
  }));
   

  function Row(props) {
    const { index, style } = props;
  
    return (
      <ListItem key={props.item.id} button>
      <ListItemAvatar>
        <Avatar
          
          src={avatar}
        />
      </ListItemAvatar>
      <ListItemText id={"1"} primary={"eklwdff"} />
      
    </ListItem>
    );
  }
  
  Row.propTypes = {
    index: PropTypes.number,
    style: PropTypes.object,
  };

export default function Commentshow(props) {
  const cuser=React.useContext(Context)
    const classes = useStyles();
  
    return (
        <div>
          {cuser &&
           <Createcomment 
           page={props.page}
           username={props.username}
           CREATE_COMMENT_MUTATION={props.CREATE_COMMENT_MUTATION}
           refetchQuery={props.refetchQuery}
           item={props.item}
           tweetid={props.tweetid}
           comments={props.comments}  
           setLoading={props.setLoading}
           />
          }
       
          <div className={props.comments.length > 10 ? classes.root : classes.showless}>
     
     {props.comments.map(item => {
        
         return (


             <ListItem key={item.id} button>
 <ListItemAvatar>
   <Avatar
     
     src={`${STATIC_URL}${item.user.profileSet[0].profilePic}`}
   />
 </ListItemAvatar>
 <ListItemText id={item.id} primary={item.comment} />
 
</ListItem>
         )
         }
     )}
  
 </div>
    </div>
    )

}