import React from 'react';
import { makeStyles } from '@material-ui/core/styles';

import ListItem from '@material-ui/core/ListItem';
import ListItemAvatar from '@material-ui/core/ListItemAvatar';
import ListItemIcon from '@material-ui/core/ListItemIcon';
import ListItemSecondaryAction from '@material-ui/core/ListItemSecondaryAction';
import ListItemText from '@material-ui/core/ListItemText';
import Avatar from '@material-ui/core/Avatar';
import IconButton from '@material-ui/core/IconButton';
import { Query, Mutation } from "react-apollo";
import { gql } from "apollo-boost";
import Typography from '@material-ui/core/Typography';
import FolderIcon from '@material-ui/icons/Folder';
import DeleteIcon from '@material-ui/icons/Delete';
import Divider from '@material-ui/core/Divider'

import Button from '@material-ui/core/Button'
import {STATIC_URL} from '../../../config/index'
import './followers.css'
const useStyles = makeStyles({
  root: {
    padding: '2px 4px',
    display: 'flex',
    alignItems: 'center',
  
  },
  input: {
    marginLeft: 8,
    flex: 8,
  },
  iconButton: {
    padding: 10,
  },
  divider: {
    width: 1,
    height: 28,
    margin: 4,
  },
  showpointer:{
    "&:hover":{
      cursor:"pointer"
    }
  }

});



 export default function Showfollowers(props) {
  const classes = useStyles();
    //console.log("shoe follower")
    //console.log(props.item)
    
    const handleFollow =async (addFollower) => {
      const res = await addFollower()
    
    }
    return (
      
       
        
           
            <div className={"givepadding"} >
              <Mutation mutation={REMOVE_FOLLOWER}
                variables={{id:props.item.id}}
                refetchQueries={()=> [{ query: props.refetchQueries}]}
                >
  
          {(addFollower, {loading, error, called, client})=> {
            if (loading) return "Loading...";
            if (error) return `Error! ${error.message}`;
              return (
                <>
                
      
             
                  <ListItem  >
                  <ListItemAvatar>
     <Avatar
     className={classes.showpointer}
       onClick={()=>props.history.push(`/profile/${props.item.id}`)}
       src={`${STATIC_URL}${props.item.profileSet[0].proflePic}`}
     />
   </ListItemAvatar>
                    <ListItemText
                       className={classes.showpointer}
                       onClick={()=>props.history.push(`/profile/${props.item.id}`)}
                      secondary={props.item.username}
                    
                    />
                    <ListItemSecondaryAction>
                    <Button 
                    onClick={()=>handleFollow(addFollower)}
                    variant="outlined" color="primary" color="primary" >
                  Follow
                  </Button>
                    </ListItemSecondaryAction>
                  </ListItem>
                  <Divider variant="inset" component="li" />
                  </>
             
             
              )
          }}
              </Mutation>
            </div>
        
   
    );
  }

  const REMOVE_FOLLOWER=gql`
  mutation($id:Int!) {
    addFollower(userToFollowId:$id) {
      user {
        id
      }
      userfollowed {
        id
      }
    }
  }
  `
  const styles = theme => ({
    showpointer:{
      '&:hover':{
        cursor:"pointer"
      }
    }
  });
  
