import React, { useState } from "react";
import { Mutation, Query, ApolloConsumer } from "react-apollo";
import { gql } from "apollo-boost";
import withStyles from "@material-ui/core/styles/withStyles";
import Typography from "@material-ui/core/Typography";
import Avatar from "@material-ui/core/Avatar";
import FormControl from "@material-ui/core/FormControl";
import Paper from "@material-ui/core/Paper";
import Input from "@material-ui/core/Input";
import InputLabel from "@material-ui/core/InputLabel";
import Button from "@material-ui/core/Button";
import {Link} from 'react-router-dom'
import Slide from "@material-ui/core/Slide";
import Gavel from "@material-ui/icons/Gavel";
import VerifiedUserTwoTone from "@material-ui/icons/VerifiedUserTwoTone";
import orange from "@material-ui/core/colors/orange";
import { Dialog, DialogTitle, DialogContent, DialogContentText, DialogActions } from "@material-ui/core";
import Singletweet from './singletweet'
import {Context} from '../../usercontext'
import InfiniteScroll from 'react-infinite-scroller';

import {MyInstagramLoader} from '../../Shared/initialLoad'
import NetworkError from '../../Shared/NetworkError'

const Tweetsheet = ({ classes, setCurrentRoute }) => {
  const cuser=React.useContext(Context)
   const [hasMore, setHasMore] =React.useState(true)
  const [page, setPage] = React.useState(1)
  return (
   
     <ApolloConsumer>
       {client => {
        
            client.writeData({data:{showProfile:false}})
           
         return (
          <div className={classes.root}>
            

          <Query query={GET_TWEETS}
          variables={{username:cuser.username, page}}
          >
         {({ loading, error, data , fetchMore}) => {
           
             if (loading) return <MyInstagramLoader />
             if (error) return <NetworkError error={error} />
     
           return (
            <div >
          

{data.usertweets.map(item => <Singletweet 
  key={item.id}
  page={page}
                refetchQuery={GET_TWEETS}
                username={cuser.username}
                item={item} />)}


              
            </div>
           );
         }}
       </Query>
            
      {
        !cuser.profileSet[0].emailverified ? (
         <div style={{position:"fixed", bottom:0, left:0, width:"100%", zIndex:999}}>
         <Paper style={{background:orange["300"],  padding:"10px"}}>
               
               <Typography variant="h6" component="h6">
                Verify your email address to post unlimited tweets.  
                 <Button
                  
                  style={{display:"inline-block"}}
                  variant="outlined"
                  color="primary"
onClick={()=> client.writeData({data:{isLoggedIn:false, authRoute:"Email"} })}              
                  className={classes.submit}
                >
                Verify Now
                </Button>
               </Typography>
             </Paper>
       
         </div>
        ) : null
      }
     
         
         </div>
         )
       }}
     </ApolloConsumer>
   
  );
};

export const GET_TWEETS = gql`
query($username:String!) 
{
    usertweets (username:$username) {
      id
        user {
            username
            profileSet {
              profilePic
            }
        }
      tweettext
      tweetcountry
      tweetfile
      tweettime
      likes {
        id
        user {
          username
          profileSet {
            profilePic
          }
        }
      }
      comments {
        id
        user {
          username
          profileSet {
            profilePic
          }
        }
        comment
      }
    }
  
}
`;

const styles = theme => ({
  root: {
    padding:20
  },
});

export default withStyles(styles)(Tweetsheet);
