import React from 'react'
import { Query, ApolloConsumer } from "react-apollo";
import { gql } from "apollo-boost";
import headerpic from '../../../assets/header.png'
import avatar from '../../../assets/avatar.jpeg'

import withStyles from "@material-ui/core/styles/withStyles";
import { Typography } from '@material-ui/core';
import Showtabs from './showtabs'
import { STATIC_URL } from '../../../config';
import VerifiedUser from '@material-ui/icons/VerifiedUser'
import Button from '@material-ui/core/Button'
import {CardElement, injectStripe} from 'react-stripe-elements';
import Payment from './payments/index'
import {Context} from '../../usercontext'
 function Profile({match, classes }) {
   
    
    const cuser=React.useContext(Context)

    const [paymentModalOpen, setPaymentModalOpen] = React.useState(false)
    return (
    <ApolloConsumer>
      {client => {
         client.writeData({data:{showProfile:true}})
        return (

          <Query query={GET_PROFILE}
          variables={{id:match.params.username}}
          >
          {({ loading, error, data }) => {
           
            if (loading) return "Loading...";
            if (error) return `Error! ${error.message}`;
             
              
            return (
            <div>
              <div className={classes.headerbox}>
                <img className={classes.headerimage} src={`${STATIC_URL}${data.userprofile.headerPic}`} />
                  <div className={classes.avatarbox}>
                      <img className={classes.avatarimage} src={`${STATIC_URL}${data.userprofile.profilePic}`} />
                      </div>
                  </div>
                <div>
                    <div style={{marginRight:"5%", marginTop:"2%"}}>
                    {data.userprofile.verified ? (
                          <Typography className={classes.headertext} variant="h5" color="textSecondary" component="h5">
                            {data.userprofile.fullname}<VerifiedUser  style={{color:"indigo", marginLeft:"5px", width:"0.8em"}}  />
                          </Typography>
                      ):  ( <Typography className={classes.headertext} variant="h5" color="textSecondary" component="h5">
                      {data.userprofile.fullname}
                    </Typography>
                      )  
                  }
                    
             
             
                 <Typography className={classes.headertext} variant="h6" color="textPrimary" component="h6">
                      
                     @ {data.userprofile.user.username}
                  </Typography>
                  
               

                  </div>
{ cuser  ? ( !data.userprofile.verified && cuser.id==match.params.username  ? (
  <div style={{display:"flex", flexDirection:"row", justifyContent:"space-between",
  padding: "16px",
  background: "#ddd",
  margin: "8px"
  }}>
      <Typography style={{flex:1}} variant="body1" color="textSecondary" component="h6">
          Pay $0.99 and get verified for unlimited tweets and other amazing features
        </Typography>
       
<Button
  color="primary"
  variant="contained"
  onClick={()=>setPaymentModalOpen(true)}
>
  Get Verified
</Button>
</div>
): null):null}
{
  cuser &&
  <Payment 
paymentModalOpen={paymentModalOpen}
handlePaymentModalClose={()=>setPaymentModalOpen(false)}
userid={cuser.id}
refetchQueries={() =>{
 
  return [{ query: GET_PROFILE, variables:{id:match.params.username} }]
  
}} 
/>
}

                
                  </div>
  
                  <div style={{marginTop:"20px"}}>
                      <Showtabs 
                      refetchQueries={()=> [{ query: GET_PROFILE, variables:{id:match.params.username} }]}
                      profile={data.userprofile}
                      userid={data.userprofile.user.id}
                      username={data.userprofile.user.username} 
                      email={data.userprofile.user.email} 
                      />
  
                  </div>
             </div>
            );
          }}
        </Query>
        )
      }}
    </ApolloConsumer>
    
    )
}


const styles = theme => ({
    headerbox :{
        position:"relative"
    },
    headerimage:{
        width:"100%",
        height: "240px"
    },
    avatarbox:{
        position: "absolute",
        top: "60%",
        left: "10%"
    },
    avatarimage:{
        width: "60%",
        borderRadius: "50%"
    },
    headertext:{
        textAlign:"right"
    }


})




export const GET_PROFILE = gql`
query($id:Int!) {
    userprofile(id:$id) {
      user {
        id
        username
        email
        tweetSet {
          id
          tweettext
        }
        followers {
          id
          user {
            email
            username
          }
        }
      }
      
      id
      fullname
      city
      state
      country
      headerPic
      profilePic
      shortdescription
      occupation
      verified
      is2fa
    }
  }
`;


export default withStyles(styles)(Profile);
