import React from 'react';
import Button from '@material-ui/core/Button';
import TextField from '@material-ui/core/TextField';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';
import DialogTitle from '@material-ui/core/DialogTitle';
import tempheader from '../../../../assets/header.png'
import avatar from '../../../../assets/avatar.jpeg'
import { Typography } from '@material-ui/core';
import withStyles from "@material-ui/core/styles/withStyles";
import { Mutation } from "react-apollo";
import { gql } from "apollo-boost";
import Edit from '@material-ui/icons/Edit'
import Axios from 'axios'
import {STATIC_URL, UPLOAD_URL} from '../../../../config/'


 function FormDialog({classes, profile, email, handleClose, refetchQueries}) {
 
  const [open, setOpen] = React.useState(true);
    const [name, setName] = React.useState(profile.fullname)
    const [emailvalue, setEmail] = React.useState(email)
    const [city, setCity] = React.useState(profile.city)
    const [state, setState] = React.useState(profile.state)
    const [country, setCountry] = React.useState(profile.country)
    const [occupation, setOccupation] = React.useState(profile.occupation)
    const [shortdescription, setDescription] = React.useState(profile.shortdescription)
    const [profilepic, setProfilePic] = React.useState(profile.profilePic)
    const [headerpic, setHeaderPic] = React.useState(profile.headerPic)
    const [headerpicChange, setHeaderPicChange] = React.useState(false)
    const [profilepicChange, setProfilePicChange] = React.useState(false)
    const [profilepicfile, setProfilePicFile] = React.useState("")
    const [headerpicfile, setHeaderPicFile] = React.useState("")
    
    let headerRef=React.createRef()
    let profileRef=React.createRef()


  const handleProfileUpdate =async (updateProfile) => {
     
      let res = await updateProfile()
    
        handleClose()
    }

const handleHeaderPicChange = (e) => {
 
  setHeaderPicChange(true)
  setHeaderPicFile(e.target.files[0])
  let a=new Date().toString().split("(")[0]
  let b= a.split(" ")
  b.pop()
  b.pop()
  let picname= b.join("-")+e.target.files[0].name

  setHeaderPic(picname)
  let data=new FormData()
  data.append("token", localStorage.getItem("authtoken"))
  data.append("name", picname)
  data.append("file",  e.target.files[0])
  Axios.post(UPLOAD_URL, data)
  .then(res => console.log(res))
  .catch(err => console.log(err))
}


const handleProfilePicChange= (e) => {
 
  setProfilePicChange(true)
  setProfilePicFile(e.target.files[0])
  let a=new Date().toString().split("(")[0]
  let b= a.split(" ")
  b.pop()
  b.pop()
  let picname= b.join("-")+e.target.files[0].name

  setProfilePic(picname)
  let data=new FormData()
  data.append("token", localStorage.getItem("authtoken"))
  data.append("file",  e.target.files[0])
  data.append("name", picname)
 Axios.post(UPLOAD_URL, data)
  .then(res => console.log(res))
  .catch(err => console.log(err))
}

  return (
    <Mutation mutation={UPDATE_PROFILE_MUTATION}
    variables={{name, email , city, state, country, occupation, 
      profilepic:profilepic,headerpic:headerpic,
      shortdescription:shortdescription}}
    refetchQueries={refetchQueries}
>

        {(updateProfile, {loading, error, called, client})=> {
            if (error) return error.message
            if (loading) return <p>Loading</p>
            return (
    <div>
     
      <Dialog open={open} onClose={handleClose}
      scroll="paper"
      aria-labelledby="form-dialog-title">
        <DialogTitle id="form-dialog-title">Subscribe</DialogTitle>
        <DialogContent dividers="paper">
        <div style={{position:"relative"}} className={classes.headerbox}>
             
             {headerpicChange  == true? (
                <img className={classes.headerimage} src={URL.createObjectURL(headerpicfile)} />

             ) : (
              <img className={classes.headerimage} src={`${STATIC_URL}${profile.headerPic}`} />
             )}
            
              <div  style={{    position: "absolute",
    bottom: "10%",
    right: "5%"}}>
                    <Edit 
              onClick={()=>headerRef.click()}
                style={{color:"indigo"}} />
                    </div>
               
        </div>
                <div className={classes.avatarbox}>
                <Typography className={classes.headertext} variant="h5" color="textSecondary" component="h5">
                    
                   Profile Pic
                </Typography>
                <div style={{position:"relative", display: "flex",
    justifyContent: "flex-end"}}>
          {profilepicChange ? (
  <img className={classes.avatarimage} src={URL.createObjectURL(profilepicfile)} />
          ): (
            <img className={classes.avatarimage} src={`${STATIC_URL}${profile.profilePic}`} />
          )}
               
                   
                    <div style={{    position: "absolute",
    bottom: "10%",
    right: "10%"}}>
                    <Edit 
              onClick={()=>profileRef.click()}
                style={{color:"indigo"}} />
                    </div>
                    </div>
                </div>
         
         
                <input  ref={input =>headerRef=input} onChange={handleHeaderPicChange} type="file" style={{display:"none"}} />
           <input ref={input =>profileRef=input} onChange={handleProfilePicChange} type="file" style={{display:"none"}} />
          <TextField
          id="outlined-name"
          label="Name"
         
            value={name}
            onChange={(e)=>setName(e.target.value)}
          margin="normal"
          variant="outlined"
          fullWidth
          />

<TextField
          id="outlined-name"
          label="Email"
          
            value={emailvalue}
            onChange={(e)=>setEmail(e.target.value)}
          margin="normal"
          variant="outlined"
          fullWidth
          />

<TextField
          id="outlined-name"
          label="City"
        
            value={city}
            onChange={(e)=>setCity(e.target.value)}
          margin="normal"
          variant="outlined"
          fullWidth
          />

<TextField
          id="outlined-name"
          label="State"
        
            value={state}
            onChange={(e)=>setState(e.target.value)}
          margin="normal"
          variant="outlined"
          fullWidth
          />

<TextField
          id="outlined-name"
          label="Country"
         
            value={country}
            onChange={(e)=>setCountry(e.target.value)}
          margin="normal"
          variant="outlined"
          fullWidth
          />

<TextField
          id="outlined-name"
          label="Occupation"
         
            value={occupation}
            onChange={(e)=>setOccupation(e.target.value)}
          margin="normal"
          variant="outlined"
          fullWidth
          />

<TextField
          id="outlined-name"
          label="Short Description"
          
            value={shortdescription}
            onChange={(e)=>setDescription(e.target.value)}
          margin="normal"
          variant="outlined"
          fullWidth
          />
           

        </DialogContent>
        <DialogActions>
          <Button onClick={handleClose} color="primary">
            Cancel
          </Button>
          <Button onClick={()=> handleProfileUpdate(updateProfile)} color="primary">
            Subscribe
          </Button>
        </DialogActions>
 
      </Dialog>

    </div>
          )
        }
    }
    </Mutation>
  );
}


const UPDATE_PROFILE_MUTATION = gql`
mutation ($name:String!, $email:String!
    $city:String!,
    $state:String!
    $country:String!
    $shortdescription:String!
    $occupation:String!,
    $profilepic:String!,
    $headerpic:String!
    ) {
    updateProfile(fullname:$name, 
        email:$email,
        city:$city,
        state:$state,
        country:$country,
        occupation:$occupation,
        shortdescription:$shortdescription,
        profilepic:$profilepic,
        headerpic:$headerpic
       ) {
        profile {
            id
          }
    }
  }
`;

const styles = theme => ({
    headerbox :{
        position:"relative"
    },
    headerimage:{
        width:"100%"
    },
    avatarbox:{
        display:"flex", 
        justifyContent:"space-between",
        alignItems:"center",
        marginTop:"10px",
        border: "1px solid indigo",
        padding: "10px"
    },
    avatarimage:{
        width: "30%",
        borderRadius: "50%",
        opacity:0.5
    },
    headertext:{
        textAlign:"right"
    },
    textField: {
        
      },


})



export default withStyles(styles)(FormDialog);
