import React from 'react'
import withStyles from "@material-ui/core/styles/withStyles";
import Paper from '@material-ui/core/Paper';
import Grid from '@material-ui/core/Grid';
import Container from '@material-ui/core/Container';
import Header from '../Shared/Header'
import Tweetsheet from  './Tweetsheet'
import {BrowserRouter, Route, withRouter} from 'react-router-dom'
import { ApolloConsumer } from 'react-apollo';
import Tweetbox  from './tweetbox'
import CnnFeed from './cnnfeed'
import FoxFeed from './foxfeed'
import EspnFeed from './espnfeed'
import Profile from './profille'
import Axios from 'axios';
import Randomuser from '../Shared/Follower'
import {Context} from '../usercontext'
import Email from './email/email'
import { Mutation, Query } from "react-apollo";
import { gql } from "apollo-boost";
import Worldtweets from './worldtweets/index'

import Typography from '@material-ui/core/Typography'
import PaypalButton from './profille/payments/paypalbutton'
import Button from '@material-ui/core/Button'
import Footer from '../Shared/footer'
import TermsandCondition from './terms-and-condition'

const UserFeed = (props) => {
 
  const cuser = React.useContext(Context)
    return (

      <ApolloConsumer>
      {client => {
       
           client.writeData({data:{showProfile:false}})
          
        return (
  <div>
    {!cuser ? (
        <Typography style={{padding:"16px"}} variant="h5" component="h5">You need to signup to have your own profile</Typography>
    ) : (
<div>
<Tweetbox  username={cuser.username}/>
          <Tweetsheet />
  </div>
    )}
        
          </div>
        )
    }
  }
  </ApolloConsumer>
    )
}

 const Feed  = (props) => {
  const cuser = React.useContext(Context)
 
   let {classes}  = props
 


  return (
    <>
 <BrowserRouter>
 <Query query={SHOW_PROFILE_QUERY}>
      {({data}) => {
        return (
          <>
<Header  currentUser={cuser} />  
    <Container className={classes.container} maxWidth="md">
      
      <Grid  container spacing={3}>
       
      <Grid item xs={12} md={data.showProfile ? 12 : 7}>
        <Paper>
         
          <Route path="/" exact component={UserFeed} />
          <Route path="/cnnfeed" component={CnnFeed} />
          <Route path="/foxfeed" component={FoxFeed} />
          <Route path="/espnfeed" component={EspnFeed} />
          <Route path="/worldtweets" component={Worldtweets} />
          <Route path="/profile/:username" component={Profile} />
          <Route path="/terms-and-conditions" component={TermsandCondition} />
        
         
        </Paper>

      </Grid>
{
  data.showProfile ? null : (
<Grid  item xs={12} md={5}>


              <Paper>
      <Randomuser />
      </Paper>

<Footer />
      </Grid>  
   
  )
}
          
     
    
  </Grid>
  </Container>

  </>
        )
      }
    }
    </Query>



  </BrowserRouter>

  </>
  )
  }


const styles = theme => ({
  bordered:{
    border:"1px solid black"
  },
  root: {
    width: "auto",
    display: "block",
    marginLeft: theme.spacing.unit * 3,
    marginRight: theme.spacing.unit * 3,
    [theme.breakpoints.up("md")]: {
      width: 400,
      marginLeft: "auto",
      marginRight: "auto"
    }
  },
  paper: {
    marginTop: theme.spacing.unit * 8,
    display: "flex",
    flexDirection: "column",
    alignItems: "center",
    padding: theme.spacing.unit * 2
  },
  title: {
    marginTop: theme.spacing.unit * 2,
    color: theme.palette.openTitle
  },
  avatar: {
    margin: theme.spacing.unit,
    backgroundColor: theme.palette.secondary.main
  },
  form: {
    width: "100%",
    marginTop: theme.spacing.unit
  },
  submit: {
    marginTop: theme.spacing.unit * 2,
    marginBottom: theme.spacing.unit * 2
  },
  icon: {
    padding: "0px 2px 2px 0px",
    verticalAlign: "middle",
    color: "green"
  },
  container :{
    marginTop:"5.5em",
  },
  paypal:{
    marginTop:theme.spacing.unit*1
  }
});

const SHOW_PROFILE_QUERY = gql`
{
  showProfile @client
}
`

export default withStyles(styles)(Feed);
