import React from 'react';
import PropTypes from 'prop-types';
import { makeStyles } from '@material-ui/core/styles';
import ListItem from '@material-ui/core/ListItem';
import ListItemText from '@material-ui/core/ListItemText';
import ListItemAvatar from '@material-ui/core/ListItemAvatar';
import { FixedSizeList } from 'react-window';
import Avatar from '@material-ui/core/Avatar';
import avatar from '../../assets/avatar.jpeg'
import { STATIC_URL } from '../../config';


const useStyles = makeStyles(theme => ({
  root: {
    width: '100%',
    height: 400,
    maxWidth: 360,
    backgroundColor: theme.palette.background.paper,
  },
  showless: {
    width:"100%",
    mexWidth:360,
    backgroundColor: theme.palette.background.paper,
  }
}));

function Row(props) {
  const { index, style } = props;

  return (
    <ListItem key={props.item.id} button>
    <ListItemAvatar>
      <Avatar
        
        src={avatar}
      />
    </ListItemAvatar>
    <ListItemText id={"1"} primary={"eklwdff"} />
    
  </ListItem>
  );
}

Row.propTypes = {
  index: PropTypes.number,
  style: PropTypes.object,
};

export default function VirtualizedList(props) {
  const classes = useStyles();

  return (
    <div className={props.likes.length > 10 ? classes.root : classes.showless}>
     
        {props.likes.map(item => {
          
            return (


                <ListItem key={item.id} button>
    <ListItemAvatar>
      <Avatar
        
        src={`${STATIC_URL}${item.user.profileSet[0].profilePic}`}
      />
    </ListItemAvatar>
    <ListItemText id={item.id} primary={"eklwdff"} />
    
  </ListItem>
            )
            }
        )}
     
    </div>
  );
}